"use client";

import { useEffect, useState } from "react";

export function useSuppressHydration() {
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  return isClient;
}

export function useGrammarlySuppression() {
  useEffect(() => {
    // Function to remove Grammarly attributes
    const removeGrammarlyAttributes = () => {
      const elements = document.querySelectorAll('[data-new-gr-c-s-check-loaded], [data-gr-ext-installed]');
      elements.forEach((element) => {
        element.removeAttribute('data-new-gr-c-s-check-loaded');
        element.removeAttribute('data-gr-ext-installed');
      });
    };

    // Run immediately
    removeGrammarlyAttributes();

    // Set up a mutation observer to handle dynamically added elements
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.type === 'attributes') {
          const target = mutation.target as Element;
          if (target.hasAttribute('data-new-gr-c-s-check-loaded') || target.hasAttribute('data-gr-ext-installed')) {
            target.removeAttribute('data-new-gr-c-s-check-loaded');
            target.removeAttribute('data-gr-ext-installed');
          }
        }
      });
    });

    observer.observe(document.body, {
      attributes: true,
      attributeFilter: ['data-new-gr-c-s-check-loaded', 'data-gr-ext-installed'],
      subtree: true,
    });

    return () => {
      observer.disconnect();
    };
  }, []);
} 